/* hero section styling */

.hero-section {
  /* background           : url('../src/components/images/Untitled.png') no-repeat center center/cover; */
  height               : 100vh;
  /* Hierdoor neemt de sectie de volledige hoogte van het scherm in beslag */
  display              : flex;
  justify-content      : center;
  align-items          : center;
  text-align           : center;
  color                : #fff;
  -webkit-touch-callout: none;
  -webkit-user-select  : none;
  -khtml-user-select   : none;
  -moz-user-select     : none;
  -ms-user-select      : none;
  user-select          : none;
  position: relative;
}

.hero-image {
  top: 0;
  left: 0;
  display: block;
  margin: 0 auto;
  transform: translateX(-16.5%);
  width: 150%;

  object-fit: cover;
  z-index: -1;
  
}

.hero-content {
  max-width: 600px;
  padding  : 20px;
 
  /* move up */
  margin-top: -150px;
}

.hero-button {
  font-family: Arial, sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  padding: 15px 30px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  background-image: linear-gradient(135deg, #0073ff, #00b2ff, #7f00ff);
  color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.hero-button:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  transform: translateY(-2px);
}

@media only screen and (max-width: 1400px) {
  .hero-section {
    background-position: center bottom -200px;
  }
}

@media only screen and (max-width: 1200px) {
  .hero-section {
    background-position: center bottom -100px;
  }
}

@media only screen and (max-width: 1000px) {
  .hero-section {
    background-position: center bottom 0px;
    background-position: center left 0px;
  }
}

@media only screen and (max-width: 750px) {
  .hero-section {
    background-position: center left 0px;

    /* make image lag */
  }
}

@media only screen and (max-width: 750px) {
  .hero-section {
    background-position: center left 0px;
    padding-top        : -150px;
  }
}



@media only screen and (max-width: 750px) {
  .hero-button {
    margin-top: 1rem;
  }

}

