/* border animatie styles */

.image-container::before {
  content      : "";
  position     : absolute;
  top          : -10px;
  left         : -10px;
  right        : -10px;
  bottom       : -10px;
  border       : 10px solid #ff5733;
  /* Kleur van de blob-rand */
  border-radius: 50%;
  z-index      : -1;
  animation    : blob-animation 5s infinite ease-in-out;
  /* Pas de animatieduur en timingfunctie aan zoals gewenst */
}

@keyframes blob-animation {

  0%,
  100% {
    transform: translate(-10px, -10px) scale(1);
  }

  50% {
    transform: translate(10px, 10px) scale(1.2);
  }
}

